// Detailmixin
import { mhDetailsApi } from '@/api/api'
export default {
    data() {
        return {
            isShow: false, //
            popup: false, //
            index: null, //
            timer: true, //
            timerFn: null, //
            isShowMark: false, //
        }
    },
    methods: {
        /**
         * 
         */
        back() {
            let url = window.sessionStorage.getItem('parentUrl')
            this.$router.push({
                path: '/about',
                query: {
                    url: url,
                },
            })
        },
        /**
         * 
         */
        init() {
            this.list = []
            this.isShow = false
            this.popup = false
            this.$nextTick(() => {
                    document.documentElement.scrollTop ?
                        (document.documentElement.scrollTop = 0) :
                        (document.body.scrollTop = 0)
                }) //
        },
        /**
         * 
         * @param {str} id id
         * @returns {array} 
         */
        async getList(id) {
            this.isShowMark = true
            this.isShow = false
            this.popup = false
            loading()
            try {
                return await mhDetailsApi(id).then((res) => {
                    let { data } = res.data
                    this.isShowMark = false
                    load.clear()
                    return data || []
                })
            } catch (err) {
                load.clear()
                this.isShowMark = false
                console.log(err)
            }
        },
        /**
         *  
         */
        cancle() {
            this.isShow = false
            this.popup = false
        },
        /**
         * 
         */
        choseType() {
            this.isShow ? (this.isShow = false) : (this.isShow = true)
        },
        /**
         * 
         * 
         */
        more() {
            let { url } = this.json
            this.mhlist = JSON.parse(window.sessionStorage.getItem('mhList'))
                // 
            this.mhlist.forEach((v, i) => {
                if (v.chapterId === url) {
                    this.index = i
                    return false
                }
            })
            this.popup = true
        },
        /**
         * 
         */
        goDetail(item) {
            Object.assign(this.json, {
                url: item.chapterId,
                num: item.title,
            })
            this.getData()
        },
        /**
         *  ()
         * 
         */
        throttle(fn, delay) {
            let valid = true
            return function() {
                if (valid) {
                    // 
                    // 
                    valid = false
                    fn.apply(this, arguments)
                    console.log('')
                    setTimeout(() => {
                        valid = true
                    }, delay)
                } else {
                    console.log('')
                }
            }
        },
        /**
         * 
         * url
         * @param {object} newVal json 
         */
        setPathUrl(newVal) {
            if (newVal && typeof newVal === 'object') {
                this.$router.push({
                    path: '/detail',
                    query: {...newVal },
                })
            }
        },
    },
    mounted() {
        window.addEventListener('scroll', this.scroll, false)
    },
    beforeDestroy() {
        /**
         * 
         */
        if (window.removeEventListener) {
            window.removeEventListener('scroll', this.scroll, false)
        } else if (window.attachEvent) {
            window.attachEvent('scroll', this.scroll, false)
        }
        console.log('scroll')
    },
    watch: {
        /**
         *  
         * url
         */
        json(newVal) {
            this.setPathUrl(newVal)
        },
    },
}